.notification {
  position: sticky;
  top: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgb(0, 0, 0);
  z-index: 999;
}

.event_name {
  color: white;
  font-size: 1.2rem;
  line-height: 0.5rem;
  text-align: left;
  font-weight: 350;
  margin-left: 4rem;
  animation: blinker 2s ease-in-out infinite;
}

.click_btn_interactive {
  font-size: 1.2rem;
  font-weight: 450;
  color: white;
  padding: 1rem;
  border-radius: 2rem;
  margin-right: 4rem;
  /* animation: expander 1.5s ease-in-out infinite; */
}

.flash_notification {
  display: flex;
  justify-content: center;
  padding-top: 0.8rem;
  padding-bottom: 0.8rem;
}

.flash_text {
  color: rgb(120, 64, 0);
  font-size: 1.8rem;
  display: inline-block;
  white-space: nowrap; /* Prevent text from wrapping */
  overflow: hidden;
  cursor: pointer;
  animation: slideIn 3s ease-in-out 3;
}

.flash_text:hover {
  text-decoration: underline;
}

@keyframes blinker {
  0% {
    opacity: 0.1;
  }
  20% {
    opacity: 1;
  }
  80% {
    opacity: 1;
  }
  100% {
    opacity: 0.1;
  }
}

@keyframes shaker {
  50% {
    transform: translateX(-10%);
  }
}

@keyframes expander {
  50% {
    transform: scale(0.95);
  }
}

@keyframes slideIn {
  0% {
    transform: translateX(-100%);
    font-style: normal;
  }
  60% {
    transform: translateX(2%);
    font-style: italic;
  }
  65% {
    transform: translateX(0%);
    font-style: normal;
  }
  100% {
    transform: translateX(0%);
    font-style: normal;
  }
}

@media screen and (max-width: 960px) {
  .notification {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    background-color: rgb(0, 0, 0);
  }
  .event_name {
    margin-left: 0;
  }
  .click_btn_interactive{
    margin-right: 0;
  }
}
