.Courses {
  background-image: linear-gradient(rgba(28, 28, 28, 0.8), rgba(0, 0, 0, 0.4)),
    url("../Assets/araja.jpg");
  background-size: cover;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 4rem;
}

.title {
  font-size: 5rem;
  color: white;
  margin-top: 4rem;
}

.course_slides {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
}

.Course_card {
  position: relative;
  background-color: rgba(0, 0, 0, 0.58);
  padding: 2rem;
  display: flex;
  text-decoration: none;
  flex-direction: column;
  color: white;
  margin-top: 3rem;
  border-radius: 1.5rem;
  min-height: 13rem;
  border: 1.5px solid rgba(255, 166, 0, 0);
  cursor: pointer;
}

.Course_card:hover{
    border: 1.5px solid orange;
}

.course_title {
  font-size: 1.2rem;
  font-weight: 700;
  color: rgb(255, 191, 73);
}

.practice_sessions {
  margin-top: 0.7rem;
  text-align: left;
}

@media screen and (max-width: 960px) {
  .title {
    margin-top: 4rem;
    font-size: 3rem;
  }
  .Course_card {
    padding: 1rem;
    margin-left: 0.5rem;
    margin-right: 0.5rem;
  }
  .course_title {
    font-size: 1.3rem;
  }
  .practice_sessions {
    font-size: 1.1rem;
  }
}
