.home{
    background-image: linear-gradient( rgba(28, 28, 28, 0.8), rgba(0, 0, 0, 0.4) ), url('../Assets/mitchel.jpg');
    background-size: cover;
    /* background-color: rgb(48, 48, 48); */
    overflow-x: hidden;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: rgb(255, 255, 255);
}

.intro{
    font-size: 4rem;
    /* color: rgb(143, 205, 255); */
}

.intro2{
    margin-top: 1rem;
    font-size: 1.6rem;
    width: 70%;
    font-weight: 300;
}

.intro2 p{
    line-height: 2.5rem;
}

@media screen and (max-width: 960px) {
    .intro{
        font-size: 3rem;
    }
    .intro2{
        width: 90%;
    }
}