nav {
  position: fixed;
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 4rem;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.658);
  z-index: 998;
}

.hero {
  margin-left: 3rem;
}

.hero img {
  height: 3.5rem;
  cursor: pointer;
}

.menu_list {
  margin-right: 3rem;
  display: flex;
  align-items: center;
}

.menu_list .tabOptions {
  border: none;
  background-color: transparent;
  font-size: 1.1rem;
  font-weight: 300;
  margin-left: 1rem;
  cursor: pointer;
  color: rgb(254, 254, 254);
  border: 1.5px solid rgba(0, 0, 125, 0);
  border-radius: 5px;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  padding-left: 0.8rem;
  padding-right: 0.8rem;
  text-decoration: none;
}

.menu_list .tabOptions:hover {
  color: black;
  background-color: rgb(199, 199, 199);
  border-radius: 2rem;
}

.menuitems {
  display: none;
}

.bar {
  display: none;
}

@media screen and (max-width: 960px) {
  nav {
    position: relative;
    top: 0;
    background-color: rgb(32, 32, 32);
  }
  .hero {
    margin-left: 1rem;
  }
  .hero img{
    height: rem;
  }
  .menu_list {
    display: none;
  }
  .bar img {
    height: 1.7rem;
  }
  .bar {
    margin-right: 0.4rem;
    font-size: 1rem;
    padding: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgb(255, 255, 255);
    transition: 0.1s ease;
    transform: rotate(0deg);
  }
  .bar.active {
    border-radius: 2rem;
    transform: rotate(90deg);
    transition: 0.1s ease;
  }
  .bar_icon {
    font-size: 3rem;
  }
  .menuitems {
    position: absolute;
    display: flex;
    flex-direction: column;
    background-color: rgb(27, 27, 27);
    top: 4rem;
    left: 100vw;
    transform: translateX(0);
    width: 12rem;
    border-bottom-left-radius: 10px;
    transition: 0.2s ease;
    z-index: 999;
  }
  .menuitems.active {
    transform: translateX(-100%);
    transition: 0.2s ease;
  }
  .menuitems .tabOptions{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 4rem;
    font-size: 1.3rem;
    font-weight: 600;
    border: none;
    background-color: rgb(33, 33, 33);
    color: white;
    text-decoration: none;
  }
}
