footer{
    background-color: rgb(0, 0, 0);
    min-height: 50vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.footer_container{
    display: flex;
    justify-content: space-evenly;
}

.footer_logo{
    min-height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.footer_logo img{
    height: 5rem;
}

.footer_info{
    min-height: 100%;
    color: white;
    display: flex;
    flex-direction: column;
    align-items: baseline;
    justify-content: center;
    font-size: 1.2rem;
}

.footer_info p{
    line-height: 3rem;
    text-align: left;
}

.copyright{
    height: 2rem;
    color: grey;
}

@media screen and (max-width: 960px) {
    .footer_container{
        display: flex;
        flex-direction: column-reverse;
        justify-content: space-between;
        min-height: 100%;
        margin-bottom: 2rem;
    }
    .footer_logo img{
        height: 3rem;
    }
    .footer_info{
        height: 10rem;
        display: flex;
        flex-direction: column;
        align-items: baseline;
        justify-content: center;
        margin-top: 3rem;
    }
    .footer_info p{
        line-height: 3rem;
        text-align: center;
        margin-left: 1rem;
        margin-right: 1rem;
        color: rgb(178, 178, 178);
        font-size: 1rem;
    }
}
