.course_single {
  background-image: linear-gradient(rgba(28, 28, 28, 0.8), rgba(0, 0, 0, 0.4)),
    url("../Assets/vladbagacian.jpg");
  background-size: cover;
}
.course_details {
  height: 25vh;
  background-color: rgba(27, 27, 27, 0.68);
  color: white;
  font-size: 3rem;
  font-weight: 400;
  display: flex;
  align-items: end;
  padding-bottom: 1.5rem;
  padding-left: 1.5rem;
}

.course_container {
  display: flex;
  padding-bottom: 2rem;
}

.course_container .details {
  display: flex;
  flex-direction: column;
  flex: 2;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  margin-top: 1rem;
}

.course_container .details p {
  text-align: left;
  font-size: 1.3rem;
  font-weight: 400;
  background-color: rgba(27, 27, 27, 0.68);
  padding: 1.5rem;
  border-radius: 15px;
  color: rgb(251, 251, 251);
  margin-bottom: 0.5rem;
}
.no_style p {
  background-color: rgb(255, 72, 72);
}

.course_container .modules {
  flex: 1;
  text-align: left;
  font-size: 1.3rem;
  font-weight: 500;
  background-color: rgba(255, 213, 0, 0.402);
  padding: 1.5rem;
  margin-top: 2rem;
  margin-left: 1rem;
  margin-right: 1rem;
  border-radius: 2rem;
}

@media screen and (max-width: 960px) {
    .course_details{
        height: 7rem;
        padding-left: 0;
        font-size: 2rem;
    }
    .course_container {
        display: flex;
        flex-direction: column;
        padding-bottom: 2rem;
      }
}
